import React, { useState, useEffect } from "react";
import { ReloadOutlined, CheckCircleOutlined, CloseCircleOutlined, ScanOutlined } from "@ant-design/icons";
import { Input, Button, Card, Row, Col, Modal } from "antd";

const Ping = () => {
  const server_list = [
    {
      id: 1,
      name: "Production Server",
      host: "thinkzone.co",
      status: false,
    },
    {
      id: 2,
      name: "Test Server",
      host: "thinkzone.in.net",
      status: false,
    },
    {
      id: 3,
      name: "Bot Server",
      host: "tatvagyan.in",
      status: false,
    },
  ];
  const [loading, setLoading] = useState(false);
  const [myCard, setMyCard] = useState(server_list);
  const [externalHost, setExternalHost] = useState("");
  const [externalHostData, setExternalHostData] = useState({
    status: false,
    duration: "0",
  });

  useEffect(() => {
    pingAllHosts();
  }, []);

  const pingAllHosts = async () => {
    setLoading(true);
    const results = await Promise.all(
      myCard.map(async (card) => {
        const result = await pingHost(card.host);
        return { ...card, status: result.status, duration: result.duration };
      })
    );
    setMyCard(results);
    setLoading(false);
  };

  const pingExternalHost = async () => {
    if (!externalHost) alert("Enter Host Name");
    else {
      setIsModalOpen(true);
      setLoading(true);
      const result = await pingHost(externalHost);
      setExternalHostData(result);
      setLoading(false);
    }
  };

  const pingHost = async (host) => {
    console.log("host: ", host);
    try {
      const startTime = new Date().getTime();
      const res = await fetch(`https://${host}`, { mode: "no-cors" });
      console.log("res: ", res);
      const endTime = new Date().getTime();
      const duration = endTime - startTime;
      //return { status: res.ok, duration };
      return { status: true, duration };
    } catch (error) {
      return { status: false };
    }
  };

  // ------------------------ Modal -------------------------
  const [isModalOpen, setIsModalOpen] = useState(false);
  const closeModal = () => {
    setIsModalOpen(false);
  };
  // --------------------------------------------------------

  return (
    <div>
      <Button onClick={pingAllHosts} disabled={loading} type="primary" icon={<ReloadOutlined />}>
        {loading ? "Pinging..." : "Refresh"}
      </Button>
      {loading ? (
        <div>Loading ...</div>
      ) : (
        <Row gutter={16}>
          {myCard.map((card) => (
            <Col span={8} key={card.id}>
              <Card title={card.name} bordered={true} style={{ width: 300 }}>
                <p>{card.host}</p>
                <p style={{ display: "flex", alignItems: "center" }}>
                  {card.status ? (
                    <CheckCircleOutlined
                      style={{
                        fontSize: "24px",
                        color: "green",
                        backgroundColor: "#e6f7e6",
                        borderRadius: "50%",
                        padding: "5px",
                        marginRight: "8px",
                      }}
                    />
                  ) : (
                    <CloseCircleOutlined
                      style={{
                        fontSize: "24px",
                        color: "red",
                        backgroundColor: "#fdecea",
                        borderRadius: "50%",
                        padding: "5px",
                        marginRight: "8px",
                      }}
                    />
                  )}
                  {card.status ? "Running" : "Unreachable"}
                </p>
                {card.status && <p>Response Time: {card.duration} ms</p>}
              </Card>
            </Col>
          ))}
        </Row>
      )}

      <p></p>
      <p></p>
      <p>Check external server status. Enter host name and click on Ping button.</p>
      <div style={{ display: "flex" }}>
        <div style={{ display: "flex" }}>
          <div>
            <Input placeholder="Basic usage" onChange={(e) => setExternalHost(e.target.value)} />
          </div>
          <div>
            <Button onClick={pingExternalHost} disabled={loading} type="primary" icon={<ScanOutlined />}>
              {loading ? "Pinging..." : "Ping"}
            </Button>
          </div>
        </div>

        <Modal title="Ping result" open={isModalOpen} onOk={closeModal} onCancel={closeModal}>
          <p>Ping reult for: {externalHost}</p>
          <p style={{ display: "flex", alignItems: "center" }}>
            {externalHostData.status ? (
              <CheckCircleOutlined
                style={{
                  fontSize: "24px",
                  color: "green",
                  backgroundColor: "#e6f7e6",
                  borderRadius: "50%",
                  padding: "5px",
                  marginRight: "8px",
                }}
              />
            ) : (
              <CloseCircleOutlined
                style={{
                  fontSize: "24px",
                  color: "red",
                  backgroundColor: "#fdecea",
                  borderRadius: "50%",
                  padding: "5px",
                  marginRight: "8px",
                }}
              />
            )}
            {externalHostData.status ? "Running" : "Unreachable"}
          </p>
          {externalHostData.status && <p>Response Time: {externalHostData.duration} ms</p>}
        </Modal>
      </div>
    </div>
  );
};

export default Ping;
